import * as axios from "axios";

const instance = axios.create({
  baseURL: "https://dry-headland-66248.herokuapp.com/api/",
});


export const ProfileAPI = {
    getAPIStatus(id){
        return instance.get(`https://dry-headland-66248.herokuapp.com/api/Ttatus/${id}`)
    },
    updateAPIStatus(id, status) {
      return instance.put(`Ttatus/${id}`, { status: status });
    },
    getAPIPosts(){
      return instance.get('Posts')
    },
    sendAPIPost(title){
      return instance.post('Posts', {title: title})
    },
    deleteAPIPost(id){
      return instance.delete(`Posts/${id}`);
    },
    setAPILikes(id, visLike, likes){
      return instance.put(`Posts/${id}`,{liked: visLike, countLikes: likes});
    }
}