import React, { useState, createRef, useEffect } from "react";
import {getPosts, sendPost, deletePost, setLikes} from '../../../../store/profile-reduser'

import * as axios from 'axios'
import  {connect}  from "react-redux";
import Profile_posts from "./Profile_posts";



class Profile_posts_cont extends React.Component{
    componentDidMount(){
        this.props.getPosts()
    }
    render(){
        return (
            <Profile_posts setLikes ={this.props.setLikes} deletePost = {this.props.deletePost} getPosts = {this.props.getPosts} sendPost = {this.props.sendPost}  Posts = {this.props.Posts}/>
        )
    }
}


let mapStateToProps = (state) =>{
    return{
        Posts: state.Profile.Posts
    }
}

let mapDispatchToProps ={
    getPosts,
    sendPost,
    deletePost,
    setLikes
}


export default connect(mapStateToProps, mapDispatchToProps)(Profile_posts_cont);
