import Profile_friends from "./Profile_friends/Profile_friends";
import Profile_follows from "./Profile_follows/Profile_follows";

const Left_side_profile = () => {
  return (
    <div className="wrapper_profile_left">
      <Profile_photo />
      <Profile_gifts />
      <Profile_friends />
      <Profile_follows />
    </div>
  );
};

export default Left_side_profile