import React from "react";
import { useState } from "react";
import * as axios from "axios";
import { useContext } from "react";
import { AuthContext } from "../../context/Auth-contex";

const AuthPage = () => {
  const auth = useContext(AuthContext);
  const [form, setForm] = useState({ email: "", password: "", status:'' });

  const changeHandler = (event) => {
    setForm({ ...form, [event.target.name]: event.target.value });
  };

  const registherHandler = async () => {
    try {
      const data = await axios.post(
        "https://dry-headland-66248.herokuapp.com/api/register",
        { ...form }
      );
      console.log("Data", data);
    } catch (e) {}
  };
  const loginHandler = async () => {
    try {
      const data = await axios.post(
        "https://dry-headland-66248.herokuapp.com/api/login",
        { ...form }
      );
      auth.login(data.data.token, data.data.userId);
      
    } catch (e) {}
  };

  return (
    <form>
      <input
        name="email"
        onChange={changeHandler}
        type="text"
        placeholder="email"
      />
      <input
        name="password"
        onChange={changeHandler}
        type="text"
        placeholder="password"
      />
      <button type="button" onClick={registherHandler}>
        Зарегестрироваться
      </button>
      <button type="button" onClick={loginHandler}>
        Войти
      </button>
    </form>
  );
};

export default AuthPage;
