import React, {useState} from "react";
import "./About_me_info.css";
import Not_reveald_about_me_info from "./not_reveald_about_me_info/Not_reveald_about_me_info";
import Reaveald_about_me_info from "./reaveald_about_me_info/Reaveald_about_me_info";


const About_me_info = () => {
  const [reaveal, setReaveal] = useState(false)
  let revealMainInfo = () =>{
    if (reaveal == false){
      setReaveal(true)
    };
    if (reaveal == true){
      setReaveal(false)
    }
  }

  return (
    <div className="profile_about_bottom">
      <Not_reveald_about_me_info  />
      <div onClick = {revealMainInfo} className="not_revealed_info_about_me">
        {!reaveal && <span>Показать подробную информацию</span>}{reaveal && <span>Скрыть подробную информацию</span>}
      </div>
      {reaveal && <Reaveald_about_me_info />}
    </div>
  );
};





export default About_me_info;
