import React, { useState, useEffect } from "react";
import "./Profile_about_top.css";
import * as axios from "axios";
import { useContext } from "react";
import { AuthContext } from "../../../../../context/Auth-contex";


const Profile_about_top = (props) => {
  const [status, setStatus] = useState(props.stat);
  const [editMode, setEditMode] = useState(true);

  useEffect(() => {
    setStatus(props.stat);
  }, [props.stat]);
  let auth = useContext(AuthContext)

  const enterEditMode = (e) => {
    if (editMode == false) {
      setEditMode(true);
    }
    if (editMode == true) {
      props.updateStatus(auth.userId, status);
      setEditMode(false);
    }
  };
  function sendStatus() {
    props.updateStatus(auth.userId, status);
    setEditMode(true);
  }
  function onStatusChange(e) {
    setStatus(e.currentTarget.value);
  }
  function deactivateEditModeEnter(event) {
    if (event.keyCode === 13) {
      setEditMode(true);
      props.updateStatus(auth.userId, status);
    }
    return;
  }
    

  return (
    <div className="profile_about_top">
      <div className="wrapper_profile_about_top">
        <div className="wrapper_online_profile_top">
          <div className="profile_about_name">
            <h1>
              Никита Великолепный
              <img src="https://vk.com/emoji/e/f09f988e.png" alt="" />
            </h1>
          </div>
          <div className="profile_about_online">
            <h1>online</h1>
          </div>
        </div>
        <div onDoubleClick={enterEditMode}>
          {editMode && (
            <h2 onClick={enterEditMode}>{props.stat || "изменить статус"}</h2>
          )}
          {!editMode && (
            <div>
              <form className="form_status">
                <input className = 'input_status'
                  onKeyDown={deactivateEditModeEnter}
                  onChange={onStatusChange}
                  autoFocus={true}
                  value={status}
                ></input>
                <button className ='button_status' type="button" onClick={sendStatus}>
                  <span className="send_status">Сохранить</span>
                </button>
              </form>
            </div>
          )}
        </div>
        <div className="palka_profile"></div>
      </div>
    </div>
  );
};
export default Profile_about_top;
