import React from "react";
import "./Header.css";
import Logo from "./logo/Logo";
import Search_header from './search_header/Search_header';
import Music from "./music/Music";
import Notifications_header from "./notifications_header/Notifications_header";
import Header_about_me from "./header_about_me/Header_about_me";


const Header = (props) => {
  return (
    <header>
      <div className="wrapper_header">
      <Logo />
      <Search_header />
        <Notifications_header />
        <Music />
        {props.isAuth && <Header_about_me />}
      </div>
    </header>
  );
};

export default Header;
