import React from 'react';
import './Sidebar.css';
import Navigation from './Navigation/Nav';
import { Redirect } from 'react-router-dom';

const Sidebar = (props) =>{
    if (props.isAuth == false){
        return  <Redirect to= {'/auth'} />
       }

    return (
        <div className = 'wrapper_sidebar'>
            <Navigation />
        </div>
    )
}


export default Sidebar