import { ProfileAPI } from "../API/api";

const SET_STATUS = "SET_STATUS";
const SET_POSTS = "SET_POSTS";

let initialState = {
  status: "",
  Posts: [],
};

export const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_STATUS: {
      return {
        ...state,
        status: action.status,
      };
    }
    case SET_POSTS: {
      return {
        ...state,
        Posts: action.Posts
      };
    }
    default:
      return state;
  }
};

export const setStatus = (status) => {
  return {
    type: SET_STATUS,
    status,
  };
};

export const setPosts = (Posts) => {
  return {
    type: SET_POSTS,
    Posts,
  };
};


export const getPosts = () => {
  return (dispatch) => {
    ProfileAPI.getAPIPosts().then((response) => {
      dispatch(setPosts(response.data));
    });
  };
};
export const sendPost = (title) => {
  return (dispatch) => {
    ProfileAPI.sendAPIPost(title)
  };
};

export const deletePost = (id) => {
  return (dispatch) => {
    ProfileAPI.deleteAPIPost(id)
  };
};
export const setLikes = (id, visLike, likes) => {
  return (dispatch) => {
    ProfileAPI.setAPILikes(id, visLike, likes)
  };
};



export const getStatus = (id) => {
  return (dispatch) => {
    ProfileAPI.getAPIStatus(id).then((response) => {
      dispatch(setStatus(response.data.status));
    });
  };
};

export const updateStatus = (userId, status) => {
  return (dispatch) => {
    ProfileAPI.updateAPIStatus(userId, status).then((response) => {
      dispatch(setStatus(status));
    });
  };
};
